.sidebar{
    flex: 1;
    border-right: 0.5px solid lightgray;
    min-height: 100vh;
    background-color: #fff;
    .top{
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        .logo{
            font-size: 20px;
            font-weight: bold;
            color: rebeccapurple;
        }
    }
    hr{
        height: 0;
        border: 0.5px solid lightgray;
    }
    .center{
        padding-left: 10px;
        ul{
            list-style: none;
            padding: 0;
            margin: 0;

            .title{
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }

            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover{
                    background-color: rgb(233, 205, 252);
                }

                .icon{
                    font-size: 18px;
                    color: rebeccapurple;
                }

                span{
                    font-size: 13px;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }
    .bottom{
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid rebeccapurple;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1){
                background-color: azure;
            }
            &:nth-child(2){
                background-color: #555;
            }

        }
    }
}